.App {
   display: flex;
   flex-direction: row;
    height: 100vh;
    background-image: url("./assets/board2.jpg");
    background-size:     cover;                   
    background-repeat:   no-repeat;
    background-position: center center;
}

#grid{
  display: grid;
  grid-template-areas:
    'navbar'
    'conteudo ';
  grid-template-rows: 7% auto;
  width: 100%;
}
#conteudo{
  grid-area: conteudo;
  background-color: rgba(27, 135, 223, 0.9)
}


#navbar{
    grid-area: navbar;
  background-color: rgba(15, 85, 143,0.9);

}